<template>
 <div class="loading">
  <img width="24" height="24" src="./loading.gif"></img>
  <h4 class="desc">{{title}}</h4>
 </div>
</template>

<script>
  export default {
    props: {
      title: {
        type: String,
        default: '正在加载中..'
      }
    }
  }
</script>

<style scoped lang="stylus">
  @import "~common/stylus/variable"

  .loading
    width:100%
    text-align:center
    .desc
      line-height: 20px
      font-size: $font-size-small
      color: $color-text-l
</style>



