<template>
  <div class="m-header">
    <div class="icon"></div>
    <div class="text">Music</div>
  </div>
</template>
<script type="text/ecmascript-6">
  export default {}
</script>
<style lang="stylus" scoped rel="stylesheet/stylus">
  @import "~common/stylus/variable.styl"
  @import "~common/stylus/mixin.styl"
  .m-header
    position:relative
    height:44px
    text-align:center
    color:$color-theme
    font-size:0
    text-align:center
    .icon
      display:inline-block
      vertical-align:top
      width:30px
      height:32px
      margin-top:6px
      margin-right:9px
      bg-image('logo')
      background-size:30px 32px
    .text
      display:inline-block
      vertical-align:top
      line-height:44px
      font-size:$font-size-large
</style>



